.footer-kasa{
    background: #000000;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 210px;
   width: 100%;
}
.footer-kasa img{
 width: 100px;
 margin-top: 40px;
 margin-bottom: 50px;

}
.footer-kasa p{
    font-style: normal;
font-weight: 500;
color: #ffffff;
font-size: 24px;
}

@media screen and (max-width: 950px) {
    .footer-kasa p{
       font-size: 12px;
    }
    .footer-kasa img{  
        margin-bottom: 20px;
       
       }
    
             }