.home-container{
   margin-top: 68px; 

   padding: 0px 68px; 
  
}
.home-card-container{
  margin-top: 48px;
  width: 100%;
  background: #F7F7F7;
  border-radius: 25px;
  min-height: 500px;
  padding: 50px 30px 0px 30px;
  margin-bottom: 40px;
}
.home-card-container ul{
  list-style-type: none;
  display: flex;
  justify-content: space-around;

  flex-wrap: wrap;
}
.home-card-container ul li{
margin-bottom: 50px;
}
.home-card-container ul a{
  text-decoration: none;
}
@media screen and (max-width: 950px) {
  .home-container{
   margin-top: 20px;
  padding: 0px 20px;
}
.home-card-container{
  padding: 0;
  margin-top: 22px;
  background: transparent;
}
.home-card-container ul li{
  margin-bottom: 20px;
  }
          }